@font-face {
  font-family: 'Inconsolata';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Inconsolata Regular'), local('Inconsolata-Regular'),
       url('/assets/fonts/inconsolata-v18-latin-regular.woff2') format('woff2');
}
@font-face {
  font-family: 'Inconsolata';
  font-style: Bold;
  font-weight: 700;
  font-display: swap;
  src: local('Inconsolata Bold'), local('Inconsolata-Bold'),
       url('/assets/fonts/inconsolata-v18-latin-700.woff2') format('woff2');
}
